<template>
  <v-container
    id="user-profile-view"
    fluid
    tag="section"
  >
    <v-row>
      <v-col
        cols="12"
        md="12"
      >
      </v-col>
      <v-col
        v-for="(chart, i) in charts"
        :key="`chart-${i}`"
        cols="12"
        md="6"
      >
          <material-chart-card
            :color="chart.color"
            :data="chart.data"
            :options="chart.options"
            :responsive-options="chart.responsiveOptions"
            :title="chart.title"
            :type="chart.type"
          >
            <template #subtitle>
              <div class="font-weight-light text--secondary">
                <div v-html="chart.subtitle" />
              </div>
            </template>

            <template #actions>
              <v-icon
                class="mr-1"
                small
              >
                mdi-clock-outline
              </v-icon>

              <span
                class="text-caption grey--text font-weight-light"
                v-text="chart.time"
              />
            </template>
          </material-chart-card>
      </v-col>
      <v-col
        cols="12"
        md="12"
      >
        <v-card>
          <v-card-text>
            <v-tabs
              v-model="muns"
              color="info"
              fixed-tabs
              centered
              align-with-title
            >
              <v-tabs-slider color="info"></v-tabs-slider>
              <v-tab>Riwayat Aktivitas</v-tab>
              <v-tab>Tanggal aktivitas</v-tab>
              <v-tab>Catatan aktivitas</v-tab>
            </v-tabs>
          <v-tabs-items v-model="muns">
            <v-tab-item
            key="1"
            >
              <v-card
                class=" p-5"
                flat
              >
                <material-card
                  color="primary"
                  class="mt-8"
                  full-header
                >
                <template #heading>
                  <div class="pa-8 white--text pm md:flex md:justify-between">
                    <div class="">
                      <div class="text-h4 font-weight-light font-semibold capitalize">
                        riwayat aktivitas
                      </div>
                    </div>
                  </div>
                </template>
                  <v-card-text>
                    <v-data-table
                      :headers="headers"
                      :items="gap"
                    >
                      <template v-slot:item.action="{ item }">
                        <v-icon
                          small
                          class="mr-2 edit"
                          @click="edit(item)"
                        >
                          mdi-pencil
                        </v-icon>
                        <v-icon
                          small
                          class="mr-2 del"
                          @click="delet(item)"
                        >
                          mdi-delete
                        </v-icon>
                        <v-icon
                          small
                          class="mr-2 sv"
                          @click="save(item)"
                        >
                        fa-solid fa-floppy-disk
                        </v-icon>
                      </template>
                    </v-data-table>
                  </v-card-text>
                </material-card>
              </v-card>
            </v-tab-item>
            <v-tab-item
            key="1"
            >
              <v-row class="fill-height p-8">
                <v-col>
                  <v-card
                    shaped
                    outlined
                  >
                    <v-card-text>
                      <v-sheet height="64">
                        <v-toolbar
                          flat
                        >
                          <v-btn
                            outlined
                            class="mr-4"
                            color="grey darken-2"
                            @click="setToday"
                          >
                            Today
                          </v-btn>
                          <v-btn
                            fab
                            text
                            small
                            color="grey darken-2"
                            @click="prev"
                          >
                            <v-icon small>
                              mdi-chevron-left
                            </v-icon>
                          </v-btn>
                          <v-btn
                            fab
                            text
                            small
                            color="grey darken-2"
                            @click="next"
                          >
                            <v-icon small>
                              mdi-chevron-right
                            </v-icon>
                          </v-btn>
                          <v-toolbar-title v-if="$refs.calendar">
                            {{ $refs.calendar.title }}
                          </v-toolbar-title>
                          <v-spacer></v-spacer>
                          <v-menu
                            bottom
                            right
                          >
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn
                                outlined
                                color="grey darken-2"
                                v-bind="attrs"
                                v-on="on"
                              >
                                <span>{{ typeToLabel[type] }}</span>
                                <v-icon right>
                                  mdi-menu-down
                                </v-icon>
                              </v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="type = 'day'">
                                <v-list-item-title>Day</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="type = 'week'">
                                <v-list-item-title>Week</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="type = 'month'">
                                <v-list-item-title>Month</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="type = '4day'">
                                <v-list-item-title>4 days</v-list-item-title>
                              </v-list-item>
                            </v-list>
                          </v-menu>
                        </v-toolbar>
                      </v-sheet>
                      <v-sheet height="600">
                        <v-calendar
                          ref="calendar"
                          v-model="focus"
                          color="primary"
                          :events="events"
                          :event-color="getEventColor"
                          :type="type"
                          @click:event="showEvent"
                          @click:more="viewDay"
                          @click:date="viewDay"
                          @change="updateRange"
                        ></v-calendar>
                        <v-menu
                          v-model="selectedOpen"
                          :close-on-content-click="false"
                          :activator="selectedElement"
                          offset-x
                        >
                          <v-card
                            color="grey lighten-4"
                            min-width="350px"
                            flat
                          >
                            <v-toolbar
                              :color="selectedEvent.color"
                              dark
                            >
                              <v-btn icon>
                                <v-icon>mdi-pencil</v-icon>
                              </v-btn>
                              <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
                              <v-spacer></v-spacer>
                              <v-btn icon>
                                <v-icon>mdi-heart</v-icon>
                              </v-btn>
                              <v-btn icon>
                                <v-icon>mdi-dots-vertical</v-icon>
                              </v-btn>
                            </v-toolbar>
                            <v-card-text>
                              <span v-html="selectedEvent.details"></span>
                            </v-card-text>
                            <v-card-actions>
                              <v-btn
                                text
                                color="secondary"
                                @click="selectedOpen = false"
                              >
                                Cancel
                              </v-btn>
                            </v-card-actions>
                          </v-card>
                        </v-menu>
                      </v-sheet>
                    </v-card-text>
                  </v-card>
                </v-col>
              </v-row>
            </v-tab-item>
            <v-tab-item
            key="1"
            >
              <v-card
                class="mt-6 p-5"
                color="grey darken-1"
                dark
              >
                <v-card-title>
                  <v-icon
                    large
                    left
                  >
                    mdi-pen
                  </v-icon>
                  <span class="text-h6 font-weight-light">Note</span>
                </v-card-title>

                <v-card-text class="text-h5 font-weight-bold">
                  "Turns out semicolon-less style is easier and safer in TS because most gotcha edge cases are type invalid as well."
                </v-card-text>

                <v-card-actions>
                  <v-list-item class="grow">
                    <v-list-item-avatar color="grey darken-3">
                      <v-img
                        class="elevation-6"
                        alt=""
                        src="https://avataaars.io/?avatarStyle=Transparent&topType=ShortHairShortCurly&accessoriesType=Prescription02&hairColor=Black&facialHairType=Blank&clotheType=Hoodie&clotheColor=White&eyeType=Default&eyebrowType=DefaultNatural&mouthType=Default&skinColor=Light"
                      ></v-img>
                    </v-list-item-avatar>

                    <v-list-item-content>
                      <v-list-item-title>Evan You</v-list-item-title>
                    </v-list-item-content>

                    <v-row
                      align="center"
                      justify="end"
                    >
                      <v-icon class="mr-1">
                        mdi-share-variant
                      </v-icon>
                      <span class="subheading">45</span>
                    </v-row>
                  </v-list-item>
                </v-card-actions>
              </v-card>
            </v-tab-item>
          </v-tabs-items>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <div class="mb-16"></div>
  </v-container>
</template>
<script>
import Vue from 'vue'
const lineSmooth = Vue.chartist.Interpolation.cardinal({
  tension: 0,
})

export default {
  name: 'PlanetChart',
  data: () => {
    return {
      charts: [
        {
          type: 'Line',
          color: 'success',
          title: 'Riwayat Aktivitas (masuk)',
          subtitle: '<i class="mdi mdi-arrow-up green--text"></i><span class="green--text">55%</span>&nbsp;increase in today\'s sales',
          time: 'updated 4 minutes ago',
          data: {
            labels: ['12am', '3pm', '6pm', '9pm', '12pm', '3am', '6am', '9am'],
            series: [
              [230, 750, 450, 300, 280, 240, 200, 190],
            ],
          },
          options: {
            lineSmooth,
            low: 0,
            high: 1000, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
            chartPadding: {
              top: 0,
              right: 0,
              bottom: 0,
              left: 0,
            },
          },
        },
        {
          type: 'Line',
          color: 'info',
          title: 'Riwayat Aktivitas (keluar)',
          subtitle: 'Last Campaign Performance',
          time: 'campaign sent 26 minutes ago',
          data: {
            labels: ['M', 'T', 'W', 'T', 'F', 'S', 'S'],
            series: [
              [12, 17, 7, 17, 23, 18, 38],
            ],
          },
          options: {
            lineSmooth,
            low: 0,
            high: 50, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
            chartPadding: {
              top: 0,
              right: 0,
              bottom: 0,
              left: 0,
            },
          },
        },
      ],
      headers: [
        {
          sortable: true,
          text: 'ID',
          value: 'id',
        },
        {
          sortable: true,
          text: 'Nama Aktivitas',
          value: 'nameactivity',
        },
        {
          sortable: false,
          text: 'Kategory',
          value: 'kategori',
        },
        {
          sortable: false,
          text: 'Riwayat Tanggal',
          value: 'tanggal',
        },
        {
          sortable: false,
          text: 'Catatan',
          value: 'catatan',
        },
      ],
      gap: [
        {
          id : 1,
          nameactivity: 'OSN Tingkat provinsi',
          kategori: 'Event',
          tanggal: '22 mei 2022 - 31 juli 2022',
          catatan: 'berhasil meraih 2 medli emas dan 3 medali perak'
        },
        {
          id : 2,
          nameactivity: 'OSN Tingkat nasional',
          kategori: 'Event',
          tanggal: '27 agustus 2022 - 11 oktober 2022',
          catatan: 'berhasil meraih 3 medali perak'
        }
      ],
      muns: 0,
      focus: '',
      type: 'month',
      typeToLabel: {
        month: 'Month',
        week: 'Week',
        day: 'Day',
        '4day': '4 Days',
      },
      selectedEvent: {},
      selectedElement: null,
      selectedOpen: false,
      events: [],
      colors: ['blue', 'indigo', 'deep-purple', 'cyan', 'green', 'orange', 'grey darken-1'],
      names: ['Meeting', 'Travel', 'Event', 'Party'],
    }
  },
  mounted () {
      this.$refs.calendar.checkChange()
    },
  methods: {
      viewDay ({ date }) {
        this.focus = date
        this.type = 'day'
      },
      getEventColor (event) {
        return event.color
      },
      setToday () {
        this.focus = ''
      },
      prev () {
        this.$refs.calendar.prev()
      },
      next () {
        this.$refs.calendar.next()
      },
      showEvent ({ nativeEvent, event }) {
        const open = () => {
          this.selectedEvent = event
          this.selectedElement = nativeEvent.target
          requestAnimationFrame(() => requestAnimationFrame(() => this.selectedOpen = true))
        }

        if (this.selectedOpen) {
          this.selectedOpen = false
          requestAnimationFrame(() => requestAnimationFrame(() => open()))
        } else {
          open()
        }

        nativeEvent.stopPropagation()
      },
      updateRange ({ start, end }) {
        const events = []

        const min = new Date(`${start.date}T00:00:00`)
        const max = new Date(`${end.date}T23:59:59`)
        const days = (max.getTime() - min.getTime()) / 86400000
        const eventCount = this.rnd(days, days + 20)

        for (let i = 0; i < eventCount; i++) {
          const allDay = this.rnd(0, 3) === 0
          const firstTimestamp = this.rnd(min.getTime(), max.getTime())
          const first = new Date(firstTimestamp - (firstTimestamp % 900000))
          const secondTimestamp = this.rnd(2, allDay ? 288 : 8) * 900000
          const second = new Date(first.getTime() + secondTimestamp)

          events.push({
            name: this.names[this.rnd(0, this.names.length - 1)],
            start: first,
            end: second,
            color: this.colors[this.rnd(0, this.colors.length - 1)],
            timed: !allDay,
          })
        }

        this.events = events
      },
      rnd (a, b) {
        return Math.floor((b - a + 1) * Math.random()) + a
      },
  },
}
</script>
<style scoped lang="scss">
.v-event-draggable {
  padding-left: 6px;
}

.v-event-timed {
  user-select: none;
  -webkit-user-select: none;
}

.v-event-drag-bottom {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 4px;
  height: 4px;
  cursor: ns-resize;

  &::after {
    display: none;
    position: absolute;
    left: 50%;
    height: 4px;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    width: 16px;
    margin-left: -8px;
    opacity: 0.8;
    content: '';
  }

  &:hover::after {
    display: block;
  }
}
</style>